import React from 'react';
import Image from 'next/image';
import close from '/public/static/img/close.svg';

import styles from './MobileMenuFilters.module.scss';

type MobileMenuFiltersTypes = {
	isOpen: boolean;
	children: React.ReactElement<any, any>;
	closeMobileMenu: () => void;
};

const MobileMenuFilters = ({ isOpen, children, closeMobileMenu }: MobileMenuFiltersTypes) => {
	return (
		<>
			{isOpen && (
				<div className={styles.menu}>
					<Image
						alt="X"
						onClick={closeMobileMenu}
						src={close}
						width={24}
						height={24}
						style={{
							maxWidth: '100%',
							height: 'auto',
						}}
					/>
					<div className={styles.content}>{children}</div>
				</div>
			)}
		</>
	);
};

export default React.memo(MobileMenuFilters);
