/* eslint-disable no-console */
import Button from 'components/UI/Button';
import Image from 'next/image';
import React from 'react';
import leftArrow from 'public/static/img/leftArrow.svg';
import rightArrow from 'public/static/img/rightArrow.svg';

import styles from './Filters.module.scss';
import Col from 'components/UI/Col';
import { useTranslation } from 'next-i18next';
import { useRouter } from 'next/router';

const selectFilter = (prev, item) => {
	if (item === 0) return [];
	const updatedSizeFilter =
		prev.find(itemPrev => itemPrev === item) !== undefined ? prev.filter(itemPrev => itemPrev !== item) : prev.concat(item);
	return updatedSizeFilter;
};

type FiltersTypes = {
	categories: {
		id: number;
		name: string;
	}[];
	dimensions: {
		id: number;
		name: string;
	}[];
	colors: {
		id: number;
		name: string;
	}[];
	changeSortTypeHandler: (sortType: { param: string; type: 'asc' | 'desc' }) => void;
	changeCategoriesHandler: (searchVal: number) => void;
	changeSizesHandler: (sizeVal: number) => void;
	changeColorsHandler: (colorsVal: number) => void;
};

const Filters = ({
	categories,
	dimensions,
	colors,
	changeSortTypeHandler,
	changeCategoriesHandler,
	changeSizesHandler,
	changeColorsHandler,
}: FiltersTypes) => {
	const [startCategoriesIndex, setStartCategoriesIndex] = React.useState<number>(0);
	const [endCategoriesIndex, setEndCategoriesIndex] = React.useState<number>(5);
	const [isLeft, setIsLeft] = React.useState<boolean | null>(null);
	const [currentCategory, setCurrentCategory] = React.useState<number[]>([]);
	const [currentDimension, setCurrentDimension] = React.useState<number[]>([]);
	const [currentColor, setCurrentColor] = React.useState<number[]>([]);
	const [currentSortType, setCurrentSortType] = React.useState<number>(0);
	const [translateCounter, setTranslateCounter] = React.useState<number>(0);

	const selectCategoryFilters = (prev, item, index) => {
		if (item === 0) return [];

		const updatedCategories =
			prev.find(itemPrev => itemPrev === index) !== undefined ? prev.filter(itemPrev => itemPrev !== index) : [index];

		return updatedCategories;
	};

	const catigoryButtonGroupRef = React.useRef<HTMLDivElement>(null);

	const { t } = useTranslation();

	const router = useRouter();

	React.useEffect(() => {
		setStartCategoriesIndex(0);
		setEndCategoriesIndex(5);
		setIsLeft(null);
		setCurrentCategory([]);
		setCurrentDimension([]);
		setCurrentColor([]);
		setCurrentSortType(0);
		setTranslateCounter(0);
		const buttonsContainer = catigoryButtonGroupRef.current.firstElementChild as HTMLDivElement;
		buttonsContainer.style.transform = 'translateX(0)';
	}, [router.query.name]);

	const onClickCategoriesHandler = ({ item, index }) => {
		setCurrentCategory(prev => selectCategoryFilters(prev, item, index));
		setCurrentDimension([]);
		setCurrentColor([]);
		const categoryFilter = selectCategoryFilters(currentCategory, item, index);
		const categoryQueryString = Array.isArray(categoryFilter) ? categoryFilter.join(',') : '';
		router.push(
			{
				pathname: router.asPath.split('?')[0],
				query: {
					dimension: [],
					category: categoryQueryString,
					color: [],
					sortParam: currentSortType === 0 ? 'date' : 'price',
					sortType: currentSortType === 2 ? 'desc' : 'asc',
				},
			},
			undefined,
			{ shallow: true }
		);
		changeCategoriesHandler(item);
	};

	const minusCategoriesIndexHandler = () => {
		if (startCategoriesIndex === 0 && translateCounter === 0) return;
		const buttonNodes = catigoryButtonGroupRef.current.firstElementChild.children;
		const buttonsContainer = catigoryButtonGroupRef.current.firstElementChild as HTMLDivElement;
		if (translateCounter !== 0) {
			let transtateValue = 0;
			Array.from(buttonNodes).forEach((item, index) => {
				if (index < translateCounter - 1) {
					transtateValue += item.getBoundingClientRect().width + 5;
				}
			});
			buttonsContainer.style.transition = '0.3s';
			buttonsContainer.style.transform = `translateX(-${transtateValue + 'px'})`;
			setTranslateCounter(prev => --prev);
			return;
		}
		setIsLeft(true);
		setStartCategoriesIndex(startCategoriesIndex - 1);
		setEndCategoriesIndex(endCategoriesIndex - 1);
	};

	const plusCategoriesIndexHandler = () => {
		const buttonNodes = catigoryButtonGroupRef.current.firstElementChild.children;
		const buttonsContainer = catigoryButtonGroupRef.current.firstElementChild as HTMLDivElement;
		if (endCategoriesIndex === categories.length - 1) {
			if (catigoryButtonGroupRef.current.getBoundingClientRect().width < buttonsContainer.getBoundingClientRect().width) {
				let transtateValue = 0;
				let totalButtonsWidth = 0;
				Array.from(buttonNodes).forEach((item, index) => {
					if (index >= translateCounter) {
						totalButtonsWidth += item.getBoundingClientRect().width + 5;
					}
					if (index < translateCounter + 1) {
						transtateValue += item.getBoundingClientRect().width + 5;
					}
				});
				if (totalButtonsWidth < catigoryButtonGroupRef.current.getBoundingClientRect().width) return;
				buttonsContainer.style.transition = '0.3s';
				buttonsContainer.style.transform = `translateX(-${transtateValue + 'px'})`;
				setTranslateCounter(prev => ++prev);
			}
			return;
		}
		buttonsContainer.style.transition = '0.3s';
		buttonsContainer.style.transform = `translateX(-${buttonNodes[0].getBoundingClientRect().width + 5 + 'px'})`;
		setTimeout(() => {
			setStartCategoriesIndex(startCategoriesIndex + 1);
			setEndCategoriesIndex(endCategoriesIndex + 1);
			buttonsContainer.style.transition = '0s';
			buttonsContainer.style.transform = `translateX(0)`;
			setIsLeft(null);
		}, 400);
	};

	const onClickDimensionsHandler = ({ item, index }) => {
		setCurrentDimension(prev => selectFilter(prev, item));
		router.push(
			{
				pathname: router.asPath.split('?')[0],
				query: {
					dimension: selectFilter(currentDimension, item).join(`,`) || [],
					category: currentCategory.join(',') || [],
					color: currentColor.join(',') || [],
					sortParam: currentSortType === 0 ? 'date' : 'price',
					sortType: currentSortType === 2 ? 'desc' : 'asc',
				},
			},
			undefined,
			{ shallow: true }
		);
		changeSizesHandler(item);
	};

	const onClickColorsHandler = ({ item, index }) => {
		setCurrentColor(prev => selectFilter(prev, item));
		router.push(
			{
				pathname: router.asPath.split('?')[0],
				query: {
					dimension: currentDimension.join(',') || [],
					category: currentCategory.join(',') || [],
					color: selectFilter(currentColor, item).join(',') || [],
					sortParam: currentSortType === 0 ? 'date' : 'price',
					sortType: currentSortType === 2 ? 'desc' : 'asc',
				},
			},
			undefined,
			{ shallow: true }
		);
		changeColorsHandler(item);
	};

	const onClickSortsHandler = (
		sortType: {
			param: string;
			type: 'asc' | 'desc';
		},
		{ index }
	) => {
		setCurrentSortType(index);
		router.push(
			{
				pathname: router.asPath.split('?')[0],
				query: {
					value: router.query.value || [],
					dimension: currentDimension.join(',') || [],
					category: currentCategory.join(',') || [],
					color: currentColor.join(',') || [],
					sortParam: sortType.param,
					sortType: sortType.type,
				},
			},
			undefined,
			{ shallow: true }
		);
		changeSortTypeHandler({ param: sortType.param, type: sortType.type });
	};

	React.useEffect(() => {
		router.query.category && setCurrentCategory((router.query.category as string).split(',').map(item => parseInt(item)));
		router.query.dimension && setCurrentDimension((router.query.dimension as string).split(',').map(item => parseInt(item)));
		router.query.color && setCurrentColor((router.query.color as string).split(',').map(item => parseInt(item)));
		router.query.sortParam &&
			router.query.sortType &&
			setCurrentSortType(router.query.sortParam === 'date' ? 0 : router.query.sortType === 'asc' ? 1 : 2);
	}, [router.query.dimension, router.query.color]);

	React.useEffect(() => {
		const buttonNodes = catigoryButtonGroupRef.current.firstElementChild.children;
		const buttonsContainer = catigoryButtonGroupRef.current.firstElementChild as HTMLDivElement;
		if (isLeft === true) {
			buttonsContainer.style.transition = '0s';
			buttonsContainer.style.transform = `translateX(-${buttonNodes[0].getBoundingClientRect().width + 5 + 'px'})`;
			setTimeout(() => {
				buttonsContainer.style.transition = '0.3s';
				buttonsContainer.style.transform = `translateX(0)`;
				setIsLeft(null);
			}, 100);
		}
	}, [isLeft]);

	return (
		<Col className={styles.filtersCol}>
			<div className={styles.filters}>
				<div className={styles.categories}>
					{Array.isArray(categories) && categories.length > 5 && (
						<Image
							alt="<"
							onClick={minusCategoriesIndexHandler}
							src={leftArrow}
							width={32}
							height={32}
							style={{
								maxWidth: '100%',
								height: 'auto',
							}}
						/>
					)}
					<div
						ref={catigoryButtonGroupRef}
						className={styles.catigoryButtonGroup}
						style={{ maxWidth: categories && categories.length <= 5 ? '100%' : '' }}
					>
						<div>
							{categories &&
								categories.length !== 0 &&
								categories.map((item, index) => {
									if (index >= startCategoriesIndex && index <= endCategoriesIndex) {
										return (
											<Button
												onClick={() => onClickCategoriesHandler({ item: item.id, index })}
												style={
													currentCategory?.length === 0 && item?.id === 0
														? { border: '1px solid #1B2530' }
														: currentCategory?.find(currentItem => index === currentItem) !==
														  undefined
														? { border: '1px solid #1B2530' }
														: {}
												}
												isInvisible
												key={item.id}
												text={item.name}
											/>
										);
									}
								})}
						</div>
					</div>
					{categories && categories.length > 5 && (
						<Image
							alt=">"
							onClick={plusCategoriesIndexHandler}
							src={rightArrow}
							width={32}
							height={32}
							style={{
								maxWidth: '100%',
								height: 'auto',
							}}
						/>
					)}
				</div>
				<div className={styles.attributes}>
					<div>
						{dimensions && Array.isArray(dimensions) && dimensions.length !== 0 && (
							<div className={styles.dimensions}>
								<p>{t('catalog:filtersAndSorts.sizes')}</p>
								<div>
									{dimensions.map((item, index) => (
										<button
											style={
												currentDimension.length === 0 && item.id === 0
													? { opacity: '1' }
													: currentDimension.find(currentItem => item.id === currentItem) !== undefined
													? { opacity: '1' }
													: {}
											}
											onClick={() => onClickDimensionsHandler({ item: item.id, index })}
											key={item.id}
										>
											{item.name}
										</button>
									))}
								</div>
							</div>
						)}
						{colors && Array.isArray(colors) && colors.length !== 0 && colors && (
							<div className={styles.colors}>
								<p>{t('catalog:filtersAndSorts.colours')}</p>
								<div>
									{colors.map((item, index) => (
										<button
											style={
												currentColor.length === 0 && item.id === 0
													? { opacity: '1' }
													: currentColor.find(currentItem => item.id === currentItem) !== undefined
													? { opacity: '1' }
													: {}
											}
											onClick={() => onClickColorsHandler({ item: item.id, index })}
											key={item.id}
										>
											{item.name}
										</button>
									))}
								</div>
							</div>
						)}
					</div>
					<div>
						<button
							style={currentSortType === 0 ? { textDecoration: 'underline' } : {}}
							onClick={() => onClickSortsHandler({ param: 'date', type: 'asc' }, { index: 0 })}
						>
							{t('catalog:filtersAndSorts.newest')}
						</button>
						<button
							style={currentSortType === 1 ? { textDecoration: 'underline' } : {}}
							onClick={() => onClickSortsHandler({ param: 'price', type: 'asc' }, { index: 1 })}
						>
							{t('catalog:filtersAndSorts.lowestPriceFirst')}
						</button>
						<button
							style={currentSortType === 2 ? { textDecoration: 'underline' } : {}}
							onClick={() => onClickSortsHandler({ param: 'price', type: 'desc' }, { index: 2 })}
						>
							{t('catalog:filtersAndSorts.highestPriceFirst')}
						</button>
					</div>
				</div>
			</div>
		</Col>
	);
};

export default React.memo(Filters);
