import routes from 'helpers/routes';
import Link from 'next/link';
import React from 'react';

import styles from './Breadcrumbs.module.scss';

type BreadcrumbsTypes = {
	links: {
		name: string;
		id: number;
		code: string;
	}[];
	isProduct?: boolean;
};

const Breadcrumbs = ({ links, isProduct }: BreadcrumbsTypes) => {
	return (
		<div className={styles.Breadcrumbs}>
			{links.map((item, index) => (
				<div key={item.name} className={styles.link}>
					{isProduct && index <= 2 && (
						<Link href={(index === 0 && item.code !== 'special' ? routes.categories : routes.catalog) + item.code}>
							{item.name}
						</Link>
					)}
					{isProduct && index > 2 && <p>{item.name}</p>}
					{!isProduct && index !== links.length - 1 && (
						<Link href={(index === 0 ? routes.categories : routes.catalog) + item.code}>{item.name}</Link>
					)}
					{!isProduct && index === links.length - 1 && <p>{item.name}</p>}
					<p>/</p>
				</div>
			))}
		</div>
	);
};

export default React.memo(Breadcrumbs);
