import Col from 'components/UI/Col';
import Title from 'components/UI/Title';
import { SuisseIntl } from 'helpers/utils/fonts';

import Image, { StaticImageData } from 'next/image';
import Link from 'next/link';
import React from 'react';
import { useSelector } from 'react-redux';
import { useRouter } from 'next/router';

import styles from './CatalogBanner.module.scss';

type CatalogBannerTypes = {
	title: string;
	name: string;
	img: string | StaticImageData;
	link: string;
};

const CatalogBanner = ({ title, name, img, link }: CatalogBannerTypes) => {
	const isMobile = useSelector((state: any) => state.isMobile.isMobile);
	const router = useRouter();

	const onClickHandler = () => {
		link && router.push(link);
	};

	return (
		<Col marginTop={isMobile ? 60 : 100} marginBottom={isMobile ? 60 : 100}>
			<div className={styles.banner} onClick={onClickHandler}>
				<div>
					<Title size={isMobile ? 16 : 20} text={title} withDot />
					<Link href={link || ''} className={SuisseIntl.className}>
						{name}
					</Link>
				</div>
				<Image
					alt={name}
					src={img}
					width={568}
					height={364}
					className={styles.bannerImg}
					style={{
						maxWidth: '100%',
						height: '100%',
					}}
				/>
			</div>
		</Col>
	);
};

export default React.memo(CatalogBanner);
