import { BannerDataModel } from 'api/banner/models/BannerModel';
import { BreadcrumbsDataModel } from 'api/breadcrumbs/models/BreadcrumbsModel';
import { CatalogFiltersDataModel } from 'api/catalog/models/CatalogFiltersModel';
import { CatalogListDataModel } from 'api/catalog/models/CatalogListModel';
import Breadcrumbs from 'components/Breadcrumbs';
import Button from 'components/UI/Button';
import Col from 'components/UI/Col';
import Content from 'components/UI/Content';
import Loader from 'components/UI/Loader';
import SkipLink from 'components/UI/SkipLink';
import Title from 'components/UI/Title';
import useAppDispatch from 'helpers/hooks/useAppDispatch';
import { SuisseIntl } from 'helpers/utils/fonts';

import { useTranslation } from 'next-i18next';
import { useRouter } from 'next/router';
import React from 'react';
import { useSelector } from 'react-redux';
import {
	changeCatalogList,
	changeCurrentPage,
	changeCategories,
	changeSortParam,
	changeSortType,
	changeTotalPages,
	getCatalogList,
	changeSizes,
	changeColors,
	getFilterList,
	changeCategoriesList,
	changeSizesList,
	changeColorsList,
} from 'store/catalog/ProductListStore';

import styles from './Catalog.module.scss';
import CatalogBanner from './views/CatalogBanner';
import Filters from './views/Filters';
import MainProductBlock from './views/MainProductBlock';
import MobileFilters from './views/MobileFilters';

type SortTypeType = {
	param: string;
	type: 'asc' | 'desc';
};

type CatalogTypes = {
	catalogList: CatalogListDataModel;
	banner: BannerDataModel;
	breadcrumbs: BreadcrumbsDataModel;
	catalogFilters: CatalogFiltersDataModel;
	isSearch: boolean;
};

const Catalog = ({ catalogList, banner, breadcrumbs, catalogFilters, isSearch }: CatalogTypes) => {
	const dispatch = useAppDispatch();

	const catalogListData = useSelector((state: any) => state.catalog.catalogList);
	const totalPages = useSelector((state: any) => state.catalog.totalPages);
	const currentPage = useSelector((state: any) => state.catalog.currentPage);
	const catalogIsLoaded = useSelector((state: any) => state.catalog.catalogIsLoaded);
	const categories = useSelector((state: any) => state.catalog.categories);
	const sizes = useSelector((state: any) => state.catalog.sizes);
	const colors = useSelector((state: any) => state.catalog.colors);
	const categoriesList = useSelector((state: any) => state.catalog.categoriesList);
	const sizesList = useSelector((state: any) => state.catalog.sizesList);
	const colorsList = useSelector((state: any) => state.catalog.colorsList);
	const sortParam = useSelector((state: any) => state.catalog.sortParam);
	const sortType = useSelector((state: any) => state.catalog.sortType);

	const [disabledLoadMore, setDisabledLoadMore] = React.useState<boolean>(true);
	const [isSSR, setIsSSR] = React.useState<boolean>(true);

	const isMobile = useSelector((state: any) => state.isMobile.isMobile);

	const router = useRouter();

	const { t } = useTranslation();

	React.useEffect(() => {
		dispatch(changeCatalogList({ catalogList: catalogList.data }));
		dispatch(changeCurrentPage(2));
		dispatch(changeTotalPages(catalogList.totalPages || 3));
		if (catalogList.totalPages <= 1) {
			setDisabledLoadMore(true);
		} else {
			setDisabledLoadMore(false);
		}
		dispatch(changeCategories([]));
		dispatch(changeSizes([]));
		dispatch(changeColors([]));
		dispatch(changeCategoriesList([]));
		dispatch(changeSizesList([]));
		dispatch(changeColorsList([]));
		dispatch(changeSortParam({ sortParam: 'date' }));
		dispatch(changeSortType({ sortType: 'asc' }));
		return () => {
			dispatch(changeCatalogList({ catalogList: [] }));
			dispatch(changeCategories([]));
			dispatch(changeSizes([]));
			dispatch(changeColors([]));
			dispatch(changeCategoriesList([]));
			dispatch(changeSizesList([]));
			dispatch(changeColorsList([]));
		};
	}, [router.query.name, router.query.value]);

	React.useEffect(() => {
		router.query.category &&
			dispatch(
				changeCategories(
					catalogFilters.categories
						.filter(
							(item, index) =>
								(router.query.category as string)
									.split(',')
									.map(item => parseInt(item))
									.indexOf(index) !== -1
						)
						.map(item => item.id)
				)
			);
		router.query.dimension &&
			dispatch(
				changeSizes(
					catalogFilters.dimensions
						.filter(
							item =>
								(router.query.dimension as string)
									.split(',')
									.map(item => parseInt(item))
									.indexOf(item.id) !== -1
						)
						.map(item => item.id)
				)
			);
		router.query.color &&
			dispatch(
				changeColors(
					catalogFilters.colors
						.filter(
							item =>
								(router.query.color as string)
									.split(',')
									.map(item => parseInt(item))
									.indexOf(item.id) !== -1
						)
						.map(item => item.id)
				)
			);
		router.query.sortParam && dispatch(changeSortParam({ sortParam: router.query.sortParam }));
		router.query.sortType && dispatch(changeSortType({ sortType: router.query.sortType }));
		router.query.currentPage && dispatch(changeCurrentPage(parseInt(router.query.currentPage as string) + 1));
		setIsSSR(false);
	}, []);

	React.useEffect(() => {
		if (catalogListData.length) {
			setDisabledLoadMore(totalPages <= Math.ceil(catalogListData.length / (isMobile ? 16 : 15)));
		}
	}, [catalogListData]);

	const loadMore = () => {
		const catalogListParams = {
			search: isSearch ? (router.query.value as string) : '',
			sortParam: sortParam,
			sortType: sortType,
			page: currentPage,
			pageSize: !isMobile ? 15 : 16,
		};
		const catalogListBody = {
			filters: {
				section: isSearch ? undefined : categories.length === 0 ? [breadcrumbs[breadcrumbs.length - 1].id] : categories,
				size: sizes.length === 0 ? undefined : sizes,
				color: colors.length === 0 ? undefined : colors,
			},
		};
		dispatch(getCatalogList({ ...catalogListParams, ...catalogListBody })).then(res => {
			router.push(
				{
					pathname: router.asPath.split('?')[0],
					query: {
						...router.query,
						name: [],
						currentPage: currentPage,
					},
				},
				undefined,
				{ shallow: true }
			);
		});
	};

	const changeCategoriesHandler = (categoriesVal: number) => {
		const currentCategories = categoriesVal === 0 || categoriesVal === categories[0] ? [] : [categoriesVal];
		dispatch(changeCatalogList({ catalogList: [] }));
		dispatch(changeCurrentPage(2));
		dispatch(changeTotalPages(3));
		dispatch(changeSizes([]));
		dispatch(changeColors([]));
		dispatch(changeCategories(currentCategories));
		const catalogListParams = {
			search: '',
			sortParam: sortParam,
			sortType: sortType,
			page: 1,
			pageSize: !isMobile ? 15 : 16,
		};
		const catalogListBody = {
			filters: {
				section: currentCategories.length === 0 ? [breadcrumbs[breadcrumbs.length - 1].id] : currentCategories,
				size: undefined,
				color: undefined,
			},
		};
		dispatch(getFilterList({ id: breadcrumbs[breadcrumbs.length - 1].id, ...catalogListBody }));
		dispatch(getCatalogList({ ...catalogListParams, ...catalogListBody })).then(item => {
			dispatch(changeTotalPages((item.payload as any).data.totalPages));
			if (1 >= (item.payload as any).data.totalPages) {
				setDisabledLoadMore(true);
				return;
			} else {
				setDisabledLoadMore(false);
			}
			dispatch(changeCurrentPage(2));
		});
	};

	const changeSizesHandler = (sizesVal: number) => {
		const currentSizes =
			sizesVal === 0
				? []
				: sizes.find(itemPrev => itemPrev === sizesVal) !== undefined
				? sizes.filter(itemPrev => itemPrev !== sizesVal)
				: sizes.concat(sizesVal);
		dispatch(changeCatalogList({ catalogList: [] }));
		dispatch(changeCurrentPage(2));
		dispatch(changeTotalPages(3));
		dispatch(changeSizes(currentSizes));
		const catalogListParams = {
			search: '',
			sortParam: sortParam,
			sortType: sortType,
			page: 1,
			pageSize: !isMobile ? 15 : 16,
		};
		const catalogListBody = {
			filters: {
				section: categories.length === 0 ? [breadcrumbs[breadcrumbs.length - 1].id] : categories,
				size: currentSizes.length === 0 ? undefined : currentSizes,
				color: colors.length === 0 ? undefined : colors,
			},
		};
		dispatch(getFilterList({ id: breadcrumbs[breadcrumbs.length - 1].id, ...catalogListBody }));
		dispatch(getCatalogList({ ...catalogListParams, ...catalogListBody })).then(item => {
			dispatch(changeTotalPages((item.payload as any).data.totalPages));
			if (1 >= (item.payload as any).data.totalPages) {
				setDisabledLoadMore(true);
				return;
			} else {
				setDisabledLoadMore(false);
			}
			dispatch(changeCurrentPage(2));
		});
	};

	const changeColorsHandler = (colorsVal: number) => {
		const currentColors =
			colorsVal === 0
				? []
				: colors.find(itemPrev => itemPrev === colorsVal) !== undefined
				? colors.filter(itemPrev => itemPrev !== colorsVal)
				: colors.concat(colorsVal);
		dispatch(changeCatalogList({ catalogList: [] }));
		dispatch(changeCurrentPage(2));
		dispatch(changeTotalPages(3));
		dispatch(changeColors(currentColors));
		const catalogListParams = {
			search: '',
			sortParam: sortParam,
			sortType: sortType,
			page: 1,
			pageSize: !isMobile ? 15 : 16,
		};
		const catalogListBody = {
			filters: {
				section: categories.length === 0 ? [breadcrumbs[breadcrumbs.length - 1].id] : categories,
				size: sizes.length === 0 ? undefined : sizes,
				color: currentColors.length === 0 ? undefined : currentColors,
			},
		};
		dispatch(getFilterList({ id: breadcrumbs[breadcrumbs.length - 1].id, ...catalogListBody }));
		dispatch(getCatalogList({ ...catalogListParams, ...catalogListBody })).then(item => {
			dispatch(changeTotalPages((item.payload as any).data.totalPages));
			if (1 >= (item.payload as any).data.totalPages) {
				setDisabledLoadMore(true);
				return;
			} else {
				setDisabledLoadMore(false);
			}
			dispatch(changeCurrentPage(2));
		});
	};

	const changeSortTypeHandler = (sortTypeVal: SortTypeType) => {
		dispatch(changeCatalogList({ catalogList: [] }));
		dispatch(changeCurrentPage(2));
		dispatch(changeTotalPages(3));
		dispatch(changeSortParam({ sortParam: sortTypeVal.param }));
		dispatch(changeSortType({ sortType: sortTypeVal.type }));
		const catalogListParams = {
			search: isSearch ? (router.query.value as string) : '',
			sortParam: sortTypeVal.param,
			sortType: sortTypeVal.type,
			page: 1,
			pageSize: !isMobile ? 15 : 16,
		};
		const catalogListBody = {
			filters: {
				section: isSearch ? undefined : categories.length === 0 ? [breadcrumbs[breadcrumbs.length - 1].id] : categories,
				size: sizes.length === 0 ? undefined : sizes,
				color: colors.length === 0 ? undefined : colors,
			},
		};
		dispatch(getCatalogList({ ...catalogListParams, ...catalogListBody })).then(item => {
			dispatch(changeTotalPages((item.payload as any).data.totalPages));
			if (1 >= (item.payload as any).data.totalPages) {
				setDisabledLoadMore(true);
				return;
			} else {
				setDisabledLoadMore(false);
			}
			dispatch(changeCurrentPage(2));
		});
	};

	return (
		<Content>
			<>
				<Col marginBottom={16}>{breadcrumbs && <Breadcrumbs links={breadcrumbs.slice(0, breadcrumbs.length - 1)} />}</Col>
				<Title
					id="catalogTitle"
					marginBottom={24}
					withSuisse
					count={catalogList.totalCounter || 0}
					size={isMobile ? 32 : 46}
					text={isSearch ? router.query.value : breadcrumbs[breadcrumbs.length - 1].name}
				/>
				<Filters
					categories={
						Array.isArray(categoriesList) && categoriesList.length === 0
							? catalogFilters
								? catalogFilters.categories
								: []
							: categoriesList
					}
					dimensions={
						Array.isArray(sizesList) && sizesList.length === 0
							? catalogFilters
								? catalogFilters.dimensions
								: []
							: sizesList
					}
					colors={
						Array.isArray(colorsList) && colorsList.length === 0
							? catalogFilters
								? catalogFilters.colors
								: []
							: colorsList
					}
					changeSortTypeHandler={changeSortTypeHandler}
					changeCategoriesHandler={changeCategoriesHandler}
					changeSizesHandler={changeSizesHandler}
					changeColorsHandler={changeColorsHandler}
				/>
				<MobileFilters
					categories={
						Array.isArray(categoriesList) && categoriesList.length === 0
							? catalogFilters
								? catalogFilters.categories
								: []
							: categoriesList
					}
					dimensions={
						Array.isArray(sizesList) && sizesList.length === 0
							? catalogFilters
								? catalogFilters.dimensions
								: []
							: sizesList
					}
					colors={
						Array.isArray(colorsList) && colorsList.length === 0
							? catalogFilters
								? catalogFilters.colors
								: []
							: colorsList
					}
					isSearch={isSearch}
					changeSortType={changeSortTypeHandler}
					changeCategories={changeCategoriesHandler}
					changeSizesHandler={changeSizesHandler}
					changeColorsHandler={changeColorsHandler}
				/>
				<MainProductBlock data={(!isSSR ? catalogListData : catalogList.data).slice(0, 8)} />
				{banner && (!isSSR ? catalogListData : catalogList.data).length !== 0 && (
					<CatalogBanner title={banner.text} name={banner.name} img={banner.bannerImg} link={banner.link} />
				)}
				{(!isSSR ? catalogListData : catalogList.data).slice(8, !isMobile ? 15 : 16).length !== 0 && (
					<MainProductBlock
						withThree
						data={(!isSSR ? catalogListData : catalogList.data).slice(8, !isMobile ? 15 : 16)}
					/>
				)}
				{currentPage - 1 !== 1 &&
					catalogListData &&
					[...Array(currentPage - 1)].map((item, index) => {
						return (
							<div
								key={
									catalogListData.slice(
										(!isMobile ? 15 : 16) * (index + 1),
										(!isMobile ? 15 : 16) * (index + 1) + 8
									)[0]
										? catalogListData.slice(
												(!isMobile ? 15 : 16) * (index + 1),
												(!isMobile ? 15 : 16) * (index + 1) + 8
										  )[0].id
										: index
								}
							>
								<MainProductBlock
									data={catalogListData.slice(
										(!isMobile ? 15 : 16) * (index + 1),
										(!isMobile ? 15 : 16) * (index + 1) + 8
									)}
								/>
								{catalogListData.slice(
									(!isMobile ? 15 : 16) * (index + 1) + 8,
									(!isMobile ? 15 : 16) * (index + 1) + (!isMobile ? 15 : 16)
								).length !== 0 && (
									<MainProductBlock
										withThree={!isMobile}
										data={catalogListData.slice(
											(!isMobile ? 15 : 16) * (index + 1) + 8,
											(!isMobile ? 15 : 16) * (index + 1) + (!isMobile ? 15 : 16)
										)}
									/>
								)}
							</div>
						);
					})}
				<Col marginBottom={isMobile ? 60 : 80}>
					<div className={styles.actions}>
						{!disabledLoadMore && catalogIsLoaded ? (
							<Button onClick={loadMore} text={t('catalog:buttons.loadMore')} isInvisible />
						) : (
							<div />
						)}
						{!catalogIsLoaded && <Loader imgHeight={36} imgWidth={125} />}
						<SkipLink className={SuisseIntl.className} skipTo="#catalogTitle">
							<button>{t('catalog:buttons.backToTop')}</button>
						</SkipLink>
					</div>
				</Col>
			</>
		</Content>
	);
};

export default React.memo(Catalog);
