import Product from 'components/ProductTile';
import Col from 'components/UI/Col';
import { SuisseIntl } from 'helpers/utils/fonts';

import React from 'react';
import { useSelector } from 'react-redux';

import styles from './MainProductBlock.module.scss';

type MainProductBlockTypes = {
	data: {
		id: number;
		title: string;
		size?: string[];
		colors?: string[];
		colorNames?: string[];
		activeColor: string;
		price: number;
		oldPrice?: number;
		isSale?: boolean;
		persentSale?: number;
		images: string[];
		secondImg?: string;
	}[];
	withThree?: boolean;
};

const MainProductBlock = ({ data, withThree }: MainProductBlockTypes) => {
	const isMobile = useSelector((state: any) => state.isMobile.isMobile);

	return (
		<Col marginBottom={isMobile ? 32 : 60}>
			<div className={styles.products}>
				{withThree && !isMobile && (
					<>
						{data && Array.isArray(data) && data.length === 0 && (
							<p className={`${styles.empty} ${SuisseIntl.className}`}>  </p>
						)}
						{data && Array.isArray(data) && data.length !== 0 && (
							<div
								className={`${styles.withThree} ${styles.productsCards} ${
									data.length < (withThree && !isMobile ? 7 : 4) ? styles.productsCardsWithoutBT : ''
								}`}
							>
								{data.slice(0, 3).map(item => (
									<Product withThree key={item.id} data={item} />
								))}
							</div>
						)}
					</>
				)}
				{Array.isArray(data) && data.length !== 0 && (
					<div
						className={`${styles.productsCards} ${
							data.length < (withThree && !isMobile ? 7 : 4) ? styles.productsCardsWithoutBT : ''
						}`}
					>
						{data && data.slice(withThree && !isMobile ? 3 : 0).map(item => <Product key={item.id} data={item} />)}
					</div>
				)}
			</div>
		</Col>
	);
};

export default React.memo(MainProductBlock);
