import React from 'react';

interface SkipLinkProps {
	className?: string;
	children: React.ReactElement;
	skipTo: string;
}

const SkipLink = ({ className, children, skipTo }: SkipLinkProps) => {
	const onClick = React.useCallback(
		(event: React.SyntheticEvent) => {
			event.preventDefault();

			const container: HTMLElement | null = document.querySelector(skipTo);

			if (container) {
				container.scrollIntoView({ behavior: 'smooth' });
			}
		},
		[skipTo]
	);

	return React.cloneElement(children, { onClick, className: className });
};

export default React.memo(SkipLink);
