import Button from 'components/UI/Button';
import Col from 'components/UI/Col';
import { useTranslation } from 'next-i18next';
import { useRouter } from 'next/router';
import React from 'react';
import MobileMenuFilters from '../MobileMenuFilters';

import styles from './MobileFilters.module.scss';

const selectFilter = (prev, item) => {
	if (item === 0) return [];
	const updatedSizeFilter =
		prev.find(itemPrev => itemPrev === item) !== undefined ? prev.filter(itemPrev => itemPrev !== item) : prev.concat(item);
	return updatedSizeFilter;
};

type MobileFilterstypes = {
	categories: {
		id: number;
		name: string;
	}[];
	dimensions: {
		id: number;
		name: string;
	}[];
	colors: {
		id: number;
		name: string;
	}[];
	isSearch: boolean;
	changeSortType: (sortType: { param: string; type: 'asc' | 'desc' }) => void;
	changeCategories: (searchVal: number) => void;
	changeSizesHandler: (sizeVal: number) => void;
	changeColorsHandler: (colorsVal: number) => void;
};

const MobileFilters = ({
	categories,
	dimensions,
	colors,
	isSearch,
	changeSortType,
	changeCategories,
	changeSizesHandler,
	changeColorsHandler,
}: MobileFilterstypes) => {
	const [mobileMenuIsOpen, setMobileMenuIsOpen] = React.useState<boolean>(false);
	const [openIsFilters, setOpenIsFilters] = React.useState<boolean>(false);
	const [currentCategory, setCurrentCategory] = React.useState<number[]>([]);
	const [currentDimension, setCurrentDimension] = React.useState<number[]>([]);
	const [currentColor, setCurrentColor] = React.useState<number[]>([]);
	const [currentSortType, setCurrentSortType] = React.useState<number>(0);

	const selectCategoryFilters = (prev, item, index) => {
		if (item === 0) return [];

		const updatedCategories =
			prev.find(itemPrev => itemPrev === index) !== undefined ? prev.filter(itemPrev => itemPrev !== index) : [index];

		return updatedCategories;
	};

	const { t } = useTranslation();

	const router = useRouter();

	React.useEffect(() => {
		setCurrentCategory([]);
		setCurrentDimension([]);
		setCurrentColor([]);
		setCurrentSortType(0);
	}, [router.query.name]);

	const onClickCategoriesHandler = ({ item, index }) => {
		setCurrentCategory(prev => selectCategoryFilters(prev, item, index));
		setCurrentDimension([]);
		setCurrentColor([]);
		const categoryFilter = selectCategoryFilters(currentCategory, item, index);
		const categoryQueryString = Array.isArray(categoryFilter) ? categoryFilter.join(',') : '';
		router.push(
			{
				pathname: router.asPath.split('?')[0],
				query: {
					dimension: [],
					category: categoryQueryString,
					color: [],
					sortParam: currentSortType === 0 ? 'date' : 'price',
					sortType: currentSortType === 2 ? 'desc' : 'asc',
				},
			},
			undefined,
			{ shallow: true }
		);
		changeCategories(item);
	};

	const onClickDimensionsHandler = ({ item, index }) => {
		setCurrentDimension(prev => selectFilter(prev, item));
		router.push(
			{
				pathname: router.asPath.split('?')[0],
				query: {
					dimension: selectFilter(currentDimension, item).join(',') || [],
					category: currentCategory.join(',') || [],
					color: currentColor.join(',') || [],
					sortParam: currentSortType === 0 ? 'date' : 'price',
					sortType: currentSortType === 2 ? 'desc' : 'asc',
				},
			},
			undefined,
			{ shallow: true }
		);
		changeSizesHandler(item);
	};

	const onClickColorsHandler = ({ item, index }) => {
		setCurrentColor(prev => selectFilter(prev, item));
		router.push(
			{
				pathname: router.asPath.split('?')[0],
				query: {
					dimension: currentDimension.join(',') || [],
					category: currentCategory.join(',') || [],
					color: selectFilter(currentColor, item).join(',') || [],
					sortParam: currentSortType === 0 ? 'date' : 'price',
					sortType: currentSortType === 2 ? 'desc' : 'asc',
				},
			},
			undefined,
			{ shallow: true }
		);
		changeColorsHandler(item);
	};

	const onClickSortsHandler = (
		sortType: {
			param: string;
			type: 'asc' | 'desc';
		},
		{ index }
	) => {
		setCurrentSortType(index);
		router.push(
			{
				pathname: router.asPath.split('?')[0],
				query: {
					value: router.query.value || [],
					dimension: currentDimension.join(',') || [],
					category: currentCategory.join(',') || [],
					color: currentColor.join(',') || [],
					sortParam: sortType.param,
					sortType: sortType.type,
				},
			},
			undefined,
			{ shallow: true }
		);
		changeSortType({ param: sortType.param, type: sortType.type });
		setMobileMenuIsOpen(false);
	};

	const openMobileMenu = (isFilters: boolean) => {
		setMobileMenuIsOpen(true);
		setOpenIsFilters(isFilters);
	};

	const closeMobileMenu = () => {
		setMobileMenuIsOpen(false);
	};

	React.useEffect(() => {
		router.query.category && setCurrentCategory((router.query.category as string).split(',').map(item => parseInt(item)));
		router.query.dimension && setCurrentDimension((router.query.dimension as string).split(',').map(item => parseInt(item)));
		router.query.color && setCurrentColor((router.query.color as string).split(',').map(item => parseInt(item)));
		router.query.sortParam &&
			router.query.sortType &&
			setCurrentSortType(router.query.sortParam === 'date' ? 0 : router.query.sortType === 'asc' ? 1 : 2);
	}, [router.query.dimension, router.query.color]);

	React.useEffect(() => {
		document.querySelector<HTMLDivElement>('header').style.zIndex = !mobileMenuIsOpen ? '' : '100001';
		document.querySelector('body').style.overflowY = mobileMenuIsOpen ? 'hidden' : 'auto';
	}, [mobileMenuIsOpen]);

	return (
		<Col className={styles.filtersCol}>
			<>
				<div className={styles.filters}>
					<div className={styles.categories}>
						{Array.isArray(categories) &&
							categories.map((item, index) => (
								<Button
									onClick={() => onClickCategoriesHandler({ item: item.id, index })}
									style={
										currentCategory.length === 0 && item.id === 0
											? { backgroundColor: '#1B2530' }
											: currentCategory.find(currentItem => index === currentItem) !== undefined
											? { backgroundColor: '#1B2530' }
											: {}
									}
									className={
										currentCategory.length === 0 && item.id === 0
											? styles.whiteText
											: currentCategory.find(currentItem => index === currentItem) !== undefined
											? styles.whiteText
											: ''
									}
									isInvisible
									key={item.id}
									text={item.name}
								/>
							))}
					</div>
					<div className={styles.attributes}>
						{!isSearch && <Button onClick={() => openMobileMenu(true)} isInvisible text="" />}
						<Button
							className={isSearch ? styles.isSearch : ''}
							onClick={() => openMobileMenu(false)}
							isInvisible
							text=""
						/>
					</div>
				</div>
				<MobileMenuFilters closeMobileMenu={closeMobileMenu} isOpen={mobileMenuIsOpen}>
					<>
						{openIsFilters ? (
							<>
								<Col marginBottom={40}>
									<div className={styles.dimensions}>
										<p>{t('catalog:filtersAndSorts.sizes')}</p>
										<div>
											{dimensions.map((item, index) => (
												<button
													style={
														currentDimension.length === 0 && item.id === 0
															? { opacity: '1' }
															: currentDimension.find(currentItem => item.id === currentItem) !==
															  undefined
															? { opacity: '1' }
															: {}
													}
													onClick={() => onClickDimensionsHandler({ item: item.id, index })}
													key={item.id}
												>
													{item.name}
												</button>
											))}
										</div>
									</div>
								</Col>
								<Col marginBottom={40}>
									<div className={styles.colors}>
										{colors.map((item, index) => (
											<button
												style={
													currentColor.length === 0 && item.id === 0
														? { opacity: '1' }
														: currentColor.find(currentItem => item.id === currentItem) !== undefined
														? { opacity: '1' }
														: {}
												}
												onClick={() => onClickColorsHandler({ item: item.id, index })}
												key={item.id}
											>
												{item.name}
											</button>
										))}
									</div>
								</Col>
							</>
						) : (
							<Col marginBottom={40}>
								<div className={styles.sort}>
									<button
										style={currentSortType === 0 ? { opacity: '1' } : {}}
										onClick={() => onClickSortsHandler({ param: 'date', type: 'asc' }, { index: 0 })}
									>
										{t('catalog:filtersAndSorts.newest')}
									</button>
									<button
										style={currentSortType === 1 ? { opacity: '1' } : {}}
										onClick={() => onClickSortsHandler({ param: 'price', type: 'asc' }, { index: 1 })}
									>
										{t('catalog:filtersAndSorts.lowestPriceFirst')}
									</button>
									<button
										style={currentSortType === 2 ? { opacity: '1' } : {}}
										onClick={() => onClickSortsHandler({ param: 'price', type: 'desc' }, { index: 2 })}
									>
										{t('catalog:filtersAndSorts.highestPriceFirst')}
									</button>
								</div>
							</Col>
						)}
					</>
				</MobileMenuFilters>
			</>
		</Col>
	);
};

export default React.memo(MobileFilters);
