import BannerNavApi from 'api/banner/BannerApi';
import BreadcrumbsApi from 'api/breadcrumbs/Breadcrumbs';
import CatalogListApi from 'api/catalog/CatalogApi';
import CatalogFiltersApi from 'api/catalog/CatalogFiltersApi';
import { ApiResp } from 'api/catalog/CatalogFiltersApi/types';
import { CatalogFiltersDataModel } from 'api/catalog/models/CatalogFiltersModel';
import MetaTagsApi from 'api/seo/MetaTagsApi';
import Footer from 'components/Footer';
import Header from 'components/Header';
import Catalog from 'components/pages/Catalog';
import useAppDispatch from 'helpers/hooks/useAppDispatch';
import { useGetUserData } from 'helpers/hooks/useGetUserData';
import { baseSSR } from 'helpers/utils/baseSSR';
import { serverSideTranslations } from 'next-i18next/serverSideTranslations';
import Head from 'next/head';
import React from 'react';
import { changeIsMobile } from 'store/isMobile/IsMobileStore';

const CatalogPage = ({ data }) => {
	const dispatch = useAppDispatch();

	useGetUserData();

	dispatch(changeIsMobile(data.isMobile));

	React.useEffect(() => {
		if (
			sessionStorage.getItem('scrollPosition_' + document.location.pathname + document.location.search) &&
			document.location.pathname !== document.location.pathname + document.location.search
		) {
			setTimeout(() => {
				window.scrollTo({
					top: parseInt(
						sessionStorage.getItem('scrollPosition_' + document.location.pathname + document.location.search)
					),
					left: 0,
					behavior: 'smooth',
				});
			}, 300);
		}
	}, []);

	return (
		<>
			<Head>
				<title>
					{data.metaTags && data.metaTags.title
						? data.metaTags.title
						: '     TOTTI'}
				</title>
				{data.breadcrumbs && (
					<meta
						name="keywords"
						content={`${data.breadcrumbs[data.breadcrumbs.length - 1].name},  ${
							data.breadcrumbs[data.breadcrumbs.length - 1].name
						},  ${data.breadcrumbs[data.breadcrumbs.length - 1].name}`}
					></meta>
				)}
				<meta
					name="description"
					content={
						data.metaTags && data.metaTags.description
							? data.metaTags.description
							: 'TOTTI I  ,  .     ,   . ,   .'
					}
				></meta>
			</Head>
			<Header />
			<Catalog
				banner={data.banner}
				breadcrumbs={data.breadcrumbs}
				catalogList={data.catalogList}
				catalogFilters={data.catalogFilters}
				isSearch={data.isSearch}
			/>
			<Footer breadcrumbs={data.breadcrumbs} withDarkBGC={true} />
		</>
	);
};

export const getServerSideProps = async ({ req, query, params, locale }) => {
	const { isMobile } = await baseSSR(req);
	const breadcrumbsApi = new BreadcrumbsApi();
	const catalogListApi = new CatalogListApi();
	const bannerNavApi = new BannerNavApi();
	const catalogFiltersApi = new CatalogFiltersApi();
	const metaTagsApi = new MetaTagsApi();
	const breadcrumbs = !!query.value ? null : await breadcrumbsApi.getBreadcrumbsData({ catalogCode: params.name });
	const catalogFilterListDataInit = {
		filters: {
			section: !!query.value ? [] : [breadcrumbs.data[breadcrumbs.data.length - 1]?.id],
		},
	};
	const catalogFiltersInit: ApiResp<CatalogFiltersDataModel> = query.value
		? ([] as unknown as ApiResp<CatalogFiltersDataModel>)
		: await catalogFiltersApi.getCatalogFiltersData(
				{ id: breadcrumbs.data[breadcrumbs.data.length - 1].id },
				catalogFilterListDataInit
		  );
	const [banner, catalogFilters, catalogList, metaTags] = await Promise.all([
		query.value
			? null
			: bannerNavApi.getBannerData({
					catalog_id: breadcrumbs.data[breadcrumbs.data.length - 1].id,
					location: '',
			  }),
		!!query.value
			? ([] as unknown as ApiResp<CatalogFiltersDataModel>)
			: catalogFiltersApi.getCatalogFiltersData(
					{ id: breadcrumbs.data[breadcrumbs.data.length - 1].id },
					{
						filters: {
							section: !!query.value
								? []
								: query.category
								? catalogFiltersInit.data.categories
										.filter(
											(item, index) =>
												query.category
													.split(',')
													.map(item => parseInt(item))
													.indexOf(index) !== -1
										)
										.map(item => item.id)
								: [breadcrumbs.data[breadcrumbs.data.length - 1].id],
						},
					}
			  ),
		query.value
			? catalogListApi.getCatalogListData(
					{ search: query.value, sortParam: 'date', sortType: 'asc', page: 1, pageSize: !isMobile ? 15 : 16 },
					{ filters: {} }
			  )
			: catalogListApi.getCatalogListData(
					{
						search: '',
						sortParam: query.sortParam || 'date',
						sortType: query.sortType || 'asc',
						page: 1,
						pageSize: !isMobile
							? query.currentPage
								? query.currentPage * 15
								: 15
							: query.currentPage
							? query.currentPage * 16
							: 16,
					},
					{
						filters: {
							section: !!query.value
								? []
								: query.category
								? catalogFiltersInit.data.categories
										.filter(
											(item, index) =>
												query.category
													.split(',')
													.map(item => parseInt(item))
													.indexOf(index) !== -1
										)
										.map(item => item.id)
								: [breadcrumbs.data[breadcrumbs.data.length - 1].id],
							size: query.dimension
								? catalogFiltersInit.data.dimensions
										.filter(
											(item, index) =>
												query.dimension
													.split(',')
													.map(item => parseInt(item))
													.indexOf(item.id) !== -1
										)
										.map(item => item.id)
								: undefined,
							color: query.color
								? catalogFiltersInit.data.colors
										.filter(
											item =>
												query.color
													.split(',')
													.map(item => parseInt(item))
													.indexOf(item.id) !== -1
										)
										.map(item => item.id)
								: undefined,
						},
					}
			  ),
		!!query.value ? null : metaTagsApi.getMetaTags({ catalogId: breadcrumbs.data[breadcrumbs.data.length - 1].id }),
	]);
	if (!Array.isArray(catalogList.data.data) || (Array.isArray(catalogList.data.data) && catalogList.data.data.length === 0)) {
		return {
			notFound: true,
		};
	}
	return {
		props: {
			data: {
				banner: banner && banner.data,
				breadcrumbs: breadcrumbs !== null ? breadcrumbs.data : breadcrumbs,
				catalogList: catalogList.data,
				catalogFilters: catalogFilters.data ? catalogFilters.data : catalogFilters,
				metaTags: metaTags && metaTags.data,
				isSearch: !!query.value,
				isMobile,
			},
			...(await serverSideTranslations(locale, ['common', 'catalog'])),
		},
	};
};

export default CatalogPage;
